body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.container {
  padding: 20px;
}
.add-equipment-wrap {
  width: 100px;
  height: 100px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.add-equipment {
  width: 70px;
  height: 70px;
  box-shadow: 0px 0px 5px 2px rgba(0,0,0,0.75);
  transition: all ease 0.2s;
  transform: rotate(-45deg);
  background-size: contain;
}

.add-equipment:hover {
  box-shadow: 0px 0px 10px 4px rgba(0,0,0,0.75);
}

.equi-img-wrap {
  width: 100px;
  height: 100px;
  display: flex;
  justify-content: center;
  align-items: center;
  background-repeat: no-repeat;
  background-position: center,center;
  background-size: 88px, 112px;
}

.equi-modal-img-wrap {
  width: 34px;
  height: 34px;
  background-repeat: no-repeat;
  background-position: center,center;
  display: flex;
  justify-content: center;
  align-items: center;
  background-size: 30px, 42px;
  margin-right: 15px;
}
.equi-modal-img-wrap img {
  width: 30px;
}

.normal {
  background-image: url(https://vignette.wikia.nocookie.net/riseofcivilizations/images/e/ec/Img_icon_item_bg_grey.png/revision/latest?cb=20180912185346),url(https://vignette.wikia.nocookie.net/riseofcivilizations/images/8/86/Item_bg_frame.png/revision/latest?cb=20180912190230);
}
.advanced {
  background-image: url(https://vignette.wikia.nocookie.net/riseofcivilizations/images/a/ac/Img_icon_item_bg_green.png/revision/latest?cb=20180912185345),url(https://vignette.wikia.nocookie.net/riseofcivilizations/images/8/86/Item_bg_frame.png/revision/latest?cb=20180912190230);
}
.elite {
  background-image: url(https://vignette.wikia.nocookie.net/riseofcivilizations/images/5/57/Img_icon_item_bg_blue.png/revision/latest?cb=20180912185346),url(https://vignette.wikia.nocookie.net/riseofcivilizations/images/8/86/Item_bg_frame.png/revision/latest?cb=20180912190230);
}
.epic {
  background-image: url(https://vignette.wikia.nocookie.net/riseofcivilizations/images/0/00/Img_icon_item_bg_purple.png/revision/latest?cb=20180912185347),url(https://vignette.wikia.nocookie.net/riseofcivilizations/images/8/86/Item_bg_frame.png/revision/latest?cb=20180912190230);
}
.legendary {
  background-image: url(https://vignette.wikia.nocookie.net/riseofcivilizations/images/8/83/Img_icon_item_bg_orange.png/revision/latest?cb=20180912185347),url(https://vignette.wikia.nocookie.net/riseofcivilizations/images/8/86/Item_bg_frame.png/revision/latest?cb=20180912190230);
}

.equi-btn-wrap {
  position: relative;
}
.equi-btn-achiv {
  position: absolute;
  bottom: -8px;
  left: -8px;
  z-index: 5;
}

@media (max-width: 465px) {
  .equi-img-wrap {
    width: 52px;
    height: 52px;
  }
}

.equi-img {
  max-width: 80%;
  max-height: 80%;
}